.notes-item{
    /*flex: 1 1 150px;*/
    display: flex;
    flex-direction: column;
    border: 1px solid black;
    border-radius: 8px;
    padding: 10px;
    /*width: 40%;*/

    /*height: 100px;*/
    word-wrap    : break-word;
    overflow-wrap: break-word;
    gap: 10px;
    position: relative;
    min-width: 150px;
    max-width: 150px;
    min-height: 175px;
    /*max-width: 150px;
    min-width: 150px;*/
    justify-self: center;
    background: #00ADB5;
    box-shadow: 2px 2px 24px rgba(0, 0, 0, 0.306);
}

p{
    text-align:left;
    background: #00ADB5;
}
.del-btn{
    display: flex;
    height: 32px;
    width: 32px;
    position: absolute;
    bottom: 0;
    right: 0;
    border-radius: 50%;
    outline: none;
    border: none;
    align-items: center;
    justify-content: center;
    background: #00ADB5;
    cursor: pointer;
}
.del-btn:active, .del-btn:hover{
    background-color: #EEEEEE;
}
.del-icon{
    width: 24px;
    height: 24px;
}