.notes-bar{
    border: 1px solid black;
    border-radius: 8px;
    padding: 10px;
    width: 40%;
    margin-top: 80px;
    background: #EEEEEE;
    box-shadow: 2px 2px 24px rgba(0, 0, 0, 0.306);
}
/*form*/
.form-style{
    display: flex;
    flex-direction: column;
    gap: 12px;
}
.title{
    border: none;
    outline: none;
    background: #EEEEEE;
}
.text-area{
    border: none;
    outline: none;
    resize: none;
    display: block;
}

.has-btn{
    display: flex;
    flex-direction: row;
    align-items:center;
    justify-content: flex-end;
    gap: 24px;
}
.title::placeholder, textarea::placeholder{
    color: black;
}